"use client";

import { useEffect, useMemo, useState } from "react";
import * as XLSX from "xlsx";

const API_BASE = "https://nhbanner-slack.onrender.com";

const BANNER_TYPES = {
  home: "üè† ÌôàÎ∞∞ÎÑà",
  floating: "üìå ÌîåÎ°úÌåÖÎ∞∞ÎÑà",
  interest: "‚≠ê Í¥ÄÏã¨Í∑∏Î£πÌÉ≠Î∞∞ÎÑà",
};

function getCurrentMonthYYYYMM() {
  const d = new Date();
  return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, "0")}`;
}

function safeString(v) {
  return typeof v === "string" ? v : v == null ? "" : String(v);
}

export default function AdminPage() {
  const [month, setMonth] = useState(getCurrentMonthYYYYMM());
  const [activeType, setActiveType] = useState("home");
  const [allData, setAllData] = useState({
    home: [],
    floating: [],
    interest: [],
  });
  const [loadError, setLoadError] = useState("");
  const [editItem, setEditItem] = useState(null);

  /* ===============================
   * Îç∞Ïù¥ÌÑ∞ Î°úÎìú
   * =============================== */
  useEffect(() => {
    async function run() {
      try {
        setLoadError("");

        const results = {};

        for (const type of Object.keys(BANNER_TYPES)) {
          const res = await fetch(
            `${API_BASE}/api/banner/${type}`,
            { cache: "no-store" }
          );

          if (!res.ok) throw new Error(`${type} API Ïã§Ìå®`);

          results[type] = await res.json();
        }

        setAllData(results);
      } catch (e) {
        setLoadError(e?.message || "Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®");
      }
    }

    run();
  }, []);

  /* ===============================
   * Ïõî ÌïÑÌÑ∞
   * =============================== */
  const filtered = useMemo(() => {
    const raw = allData[activeType] || [];

    return raw
      .filter((item) =>
        safeString(item.startDate).startsWith(month)
      )
      .sort((a, b) =>
        safeString(a.startDate).localeCompare(
          safeString(b.startDate)
        )
      )
      .map((item, idx) => ({
        no: idx + 1,
        ...item,
      }));
  }, [allData, activeType, month]);

  /* ===============================
   * ÏàòÏ†ï
   * =============================== */
  function handleEdit(item) {
    setEditItem({ ...item });
  }

  async function saveEdit() {
    await fetch(`${API_BASE}/api/admin/update`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        type: activeType,
        id: editItem.id,
        updatedData: editItem,
      }),
    });

    location.reload();
  }

  /* ===============================
   * ÏÇ≠Ï†ú
   * =============================== */
  async function handleDelete(item) {
    if (!confirm("Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) return;

    await fetch(`${API_BASE}/api/admin/delete`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        type: activeType,
        id: item.id,
      }),
    });

    location.reload();
  }

  /* ===============================
   * ÏóëÏÖÄ Îã§Ïö¥Î°úÎìú
   * =============================== */
  function downloadExcel() {
    const wb = XLSX.utils.book_new();

    const rows = filtered.map((item) => ({
      No: item.no,
      EventCode: item.targetEventCode,
      Î∞∞ÎÑàÍµ¨Î∂Ñ: item.bannerCategory,
      Îß§Ï≤¥Ïú†Ìòï: item.mediaType,
      Î∞∞ÎÑàÎ™Ö: item.banner,
      Î∞∞ÎÑàÎÇ¥Ïö©: item.bannerContent,
      ÎÖ∏Ï∂úÏãúÏûë: item.startDate,
      ÎÖ∏Ï∂úÏ¢ÖÎ£å: item.endDate,
      Î∞îÎ°úÍ∞ÄÍ∏∞ÏÜçÏÑ±: item.linkType,
      ÎßÅÌÅ¨: item.linkUrl,
      ÎßÅÌÅ¨Îç∞Ïù¥ÌÑ∞: item.linkData,
      CreatedAt: item.createdAt,
    }));

    const ws = XLSX.utils.json_to_sheet(rows);
    XLSX.utils.book_append_sheet(wb, ws, BANNER_TYPES[activeType]);
    XLSX.writeFile(wb, `banner_admin_${month}.xlsx`);
  }

  return (
    <main style={{ padding: 40, fontFamily: "sans-serif" }}>
      <h1 style={{ fontSize: 26, marginBottom: 20 }}>
        üõ† Î∞∞ÎÑà Í¥ÄÎ¶¨Ïûê ÌôîÎ©¥
      </h1>

      {/* ÌÉÄÏûÖ ÌÉ≠ */}
      <div style={{ display: "flex", gap: 10, marginBottom: 20 }}>
        {Object.entries(BANNER_TYPES).map(([type, label]) => (
          <button
            key={type}
            onClick={() => setActiveType(type)}
            style={{
              padding: "8px 14px",
              cursor: "pointer",
              background:
                activeType === type ? "#222" : "#eee",
              color:
                activeType === type ? "#fff" : "#000",
              border: "none",
              borderRadius: 6,
            }}
          >
            {label}
          </button>
        ))}
      </div>

      {/* Ïõî ÌïÑÌÑ∞ */}
      <div style={{ display: "flex", gap: 12, marginBottom: 16 }}>
        <input
          type="month"
          value={month}
          onChange={(e) => setMonth(e.target.value)}
        />

        <button onClick={downloadExcel}>
          ‚¨á ÏóëÏÖÄ Îã§Ïö¥Î°úÎìú
        </button>

        <span>({filtered.length}Í±¥)</span>
      </div>

      {loadError && (
        <div style={{ color: "red" }}>‚ùå {loadError}</div>
      )}

      {/* ÌÖåÏù¥Î∏î */}
      <table
        border="1"
        cellPadding="8"
        style={{
          borderCollapse: "collapse",
          width: "100%",
          textAlign: "center",
        }}
      >
        <thead>
          <tr>
            <th>Í¥ÄÎ¶¨</th>
            <th>No</th>
            <th>EventCode</th>
            <th>Î∞∞ÎÑàÍµ¨Î∂Ñ</th>
            <th>Îß§Ï≤¥Ïú†Ìòï</th>
            <th>Î∞∞ÎÑàÎ™Ö</th>
            <th>Î∞∞ÎÑàÎÇ¥Ïö©</th>
            <th>ÎÖ∏Ï∂úÏãúÏûë</th>
            <th>ÎÖ∏Ï∂úÏ¢ÖÎ£å</th>
            <th>Î∞îÎ°úÍ∞ÄÍ∏∞ÏÜçÏÑ±</th>
            <th>ÎßÅÌÅ¨</th>
            <th>ÎßÅÌÅ¨Îç∞Ïù¥ÌÑ∞</th>
            <th>CreatedAt</th>
          </tr>
        </thead>

        <tbody>
          {filtered.length === 0 ? (
            <tr>
              <td colSpan="13">Îç∞Ïù¥ÌÑ∞ ÏóÜÏùå</td>
            </tr>
          ) : (
            filtered.map((item) => (
              <tr key={item.id}>
                <td>
                  <button onClick={() => handleEdit(item)}>ÏàòÏ†ï</button>
                  <button
                    onClick={() => handleDelete(item)}
                    style={{ color: "red" }}
                  >
                    ÏÇ≠Ï†ú
                  </button>
                </td>
                <td>{item.no}</td>
                <td>{item.targetEventCode}</td>
                <td>{item.bannerCategory}</td>
                <td>{item.mediaType}</td>
                <td>{item.banner}</td>
                <td>{item.bannerContent}</td>
                <td>{item.startDate}</td>
                <td>{item.endDate}</td>
                <td>{item.linkType}</td>
                <td>{item.linkUrl}</td>
                <td>{item.linkData}</td>
                <td>{item.createdAt}</td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/* ÏàòÏ†ï Î™®Îã¨ */}
      {editItem && (
        <div style={{
          position: "fixed",
          inset: 0,
          background: "rgba(0,0,0,0.4)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}>
          <div style={{
            background: "#fff",
            padding: 20,
            width: 400,
          }}>
            <h3>Î∞∞ÎÑà ÏàòÏ†ï</h3>

            <input
              value={editItem.banner}
              onChange={(e) =>
                setEditItem({ ...editItem, banner: e.target.value })
              }
              style={{ width: "100%", marginBottom: 10 }}
            />

            <button onClick={saveEdit}>Ï†ÄÏû•</button>
            <button onClick={() => setEditItem(null)}>
              Ï∑®ÏÜå
            </button>
          </div>
        </div>
      )}
    </main>
  );
}
